import {Button, Collapse, Paper, Typography} from "@material-ui/core";
import React, {useState} from "react";
import {useIsFetching, useQueryClient} from "react-query";
import {useDispatch, useSelector} from "react-redux";
import "./ActionBar.css";
import AddOrderForm from "./AddOrderForm";
import {SET_ACTIVE_SORTING} from "./context/actions";
import PaymentForm from "./PaymentForm";
import SortForm from "./SortForm";

export default function ActionBar() {
  const [currentTab, setTab] = useState(0);
  const queryClient = useQueryClient();
  const dispatch = useDispatch();
  const isFetching = useIsFetching();
  const authState = useSelector((store) => store.auth);
  const sortState = useSelector((state) => state.sortSelections);

  const open = (tabId) => (currentTab === tabId ? setTab(0) : setTab(tabId));

  const invalidateAddOrderValues = () => {
    queryClient.invalidateQueries("customers");
    queryClient.invalidateQueries("ce-links");
    queryClient.invalidateQueries("cp-links");
    if (!isFetching) dispatch(SET_ACTIVE_SORTING("main"));
  };

  const invalidateSortValues = () => {
    queryClient.invalidateQueries("customers");
    queryClient.invalidateQueries("statuses");
    if (!isFetching) dispatch(SET_ACTIVE_SORTING("sort"));
  };

  const invalidatePaymentValues = () => {
    queryClient.invalidateQueries("customers");
    if (!isFetching) dispatch(SET_ACTIVE_SORTING("payment"));
  };

  const showCustomerInfo = () => {
    if (!sortState[sortState.active].customer) return;
    return sortState[sortState.active].customer?.debtor ? (
      <>
        <Typography variant="subtitle1">
          <img
            className="debtor-mark"
            src="img/Blackmark.svg"
            width="19"
            height="19"
            alt="blackmark"
          />{" "}
          <strong>{sortState[sortState.active].customer?.customerName}</strong>{" "}
          {sortState[sortState.active].customer?.customerGroup}:{" "}
          {sortState[sortState.active].customer?.info}
        </Typography>
      </>
    ) : (
      <>
        <Typography variant="subtitle1">
          <strong>{sortState[sortState.active].customer?.customerName}</strong>{" "}
          {sortState[sortState.active].customer?.customerGroup}:{" "}
          {sortState[sortState.active].customer?.info}
        </Typography>
      </>
    );
  };

  return (
    <Paper elevation={2}>
      <div className="action_bar_container">
        <div className="action__bar">
          <div className="action__bar__info">{showCustomerInfo()}</div>
          <div className="action__bar__button">
            {!authState.roles.includes("ROLE_GROUNDFLOOR") &&
              !authState.roles.includes("ROLE_UVPRINT") && (
                <Button variant="contained" color="primary" onClick={() => open(1)}>
                  Додати замовлення
                </Button>
              )}
            <Button
              style={{marginLeft: ".5rem"}}
              variant="contained"
              color="primary"
              onClick={() => open(2)}
            >
              Сортування
            </Button>
            {!authState.roles.includes("ROLE_GROUNDFLOOR") &&
              !authState.roles.includes("ROLE_UVPRINT") && (
                <Button
                  style={{marginLeft: ".5rem"}}
                  variant="contained"
                  color="primary"
                  onClick={() => open(3)}
                >
                  Внести оплату
                </Button>
              )}
          </div>
        </div>

        <Collapse
          in={currentTab === 1}
          timeout="auto"
          onEntered={invalidateAddOrderValues}
        >
          <AddOrderForm />
        </Collapse>

        <Collapse
          in={currentTab === 2}
          timeout="auto"
          onEntered={invalidateSortValues}
        >
          <SortForm />
        </Collapse>

        <Collapse
          in={currentTab === 3}
          timeout="auto"
          onEntered={invalidatePaymentValues}
        >
          <PaymentForm />
        </Collapse>
      </div>
    </Paper>
  );
}
