import {Link, Typography} from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import Button from "@material-ui/core/Button";
import Paper from "@material-ui/core/Paper";
import {makeStyles} from "@material-ui/core/styles";
import Tab from "@material-ui/core/Tab";
import Tabs from "@material-ui/core/Tabs";
import Toolbar from "@material-ui/core/Toolbar";
import React from "react";
import {useQueryClient} from "react-query";
import {useHistory} from "react-router-dom";
import "./Header.css";
import * as AuthService from "./services/auth-service";

const useStyles = makeStyles(() => ({
  root: {
    flexGrow: 1,
  },
  logoutButton: {
    padding: 0,
  },
  logoutHolder: {
    display: "flex",
    justifyContent: "flex-end",
    alignItems: "baseline",
    whiteSpace: "nowrap",
  },
  tabs: {
    margin: "0 auto",
    cursor: "pointer",
  },
}));

export default function Header() {
  const classes = useStyles();
  const history = useHistory();
  const queryClient = useQueryClient();
  const [value, setValue] = React.useState(0);

  const logout = () => {
    AuthService.unauthorize();
    history.push("/login");
  };

  const changeView = (_e, newValue) => {
    setValue(newValue);
    switch (newValue) {
      case 0:
        history.push("/");
        break;
      case 1:
        queryClient.invalidateQueries("customers");
        history.push("/clients");
        break;
      default:
        break;
    }
  };

  return (
    <Paper elevation={2}>
      <div className="header__container">
        <AppBar position="static">
          <Toolbar>
            <Link color="inherit" href="https://psdruk.com.ua/">
              <img width="110" height="50" src="/img/logo.svg" alt="logo" />
            </Link>

            <Tabs
              className={classes.tabs}
              value={value}
              onChange={changeView}
              TabIndicatorProps={{style: {background: "white"}}}
              indicatorColor="secondary"
              aria-label="scrollable auto tabs example"
            >
              <Tab label="Головна" />
              {/* <Tab label="Сортування" /> */}
              <Tab label="Клієнти" />
              {/* <Tab label="Статус" /> */}
            </Tabs>

            <div className={classes.logoutHolder}>
              <Typography variant="body1" style={{fontWeight: 300}}>
                Вітаємо, <b>{localStorage.getItem("username")}</b>
              </Typography>
              <Button
                color="inherit"
                onClick={logout}
                className={classes.logoutButton}
              >
                Вийти
              </Button>
            </div>
          </Toolbar>
        </AppBar>
      </div>
    </Paper>
  );
}
