import {Button} from "@material-ui/core";
import Popper from "@material-ui/core/Popper";
import {makeStyles} from "@material-ui/core/styles";
import React from "react";
import useSetStatus from "./hooks/useSetStatus";

const sources = [
  "",
  "img/on-agreement.svg",
  "img/in-progress.svg",
  "img/ready.svg",
  "img/completed.svg",
];

const useStyles = makeStyles((theme) => ({
  container: {
    padding: theme.spacing(1),
    textAlign: "center",
    backgroundColor: "#fff",
    boxShadow: "0 0 14px 2px rgba(0, 0, 0, 0.3)",
    borderRadius: "4px",
  },
  acceptanceButton: {
    width: "100%",
    margin: ".2rem 0",
    backgroundColor: "#fff",
    border: "1px solid",
    borderColor: "#df16a0",
    color: "#df16a0",

    "&:hover": {
      color: "#fff",
      backgroundColor: "#df16a0",
    },
  },
  inWorkButton: {
    width: "100%",
    margin: ".2rem 0",
    backgroundColor: "#fff",
    border: "1px solid",
    borderColor: theme.palette.primary.main,
    color: theme.palette.primary.main,

    "&:hover": {
      color: "#fff",
      backgroundColor: theme.palette.primary.main,
    },
  },
  readyButton: {
    width: "100%",
    margin: ".2rem 0",
    backgroundColor: "#fff",
    border: "1px solid",
    borderColor: "#28a745",
    color: "#28a745",

    "&:hover": {
      color: "#fff",
      backgroundColor: "#28a745",
    },
  },
  completedButton: {
    width: "100%",
    margin: ".2rem 0",
    backgroundColor: "#28a745",
    border: "1px solid",
    borderColor: "#28a745",
    color: "#fff",

    "&:hover": {
      borderColor: "#218838",
      backgroundColor: "#218838",
    },
  },
  popper: {
    zIndex: 1,
    marginLeft: "0.5rem",
    '&[x-placement*="right"] $arrow': {
      left: 0,
      marginLeft: "-0.9em",
      marginTop: "0.45rem",
      height: "3em",
      width: "1em",
      "&::before": {
        borderWidth: "1em 1em 1em 0",
        borderColor: `transparent ${theme.palette.background.paper} transparent transparent`,
      },
    },
    '&[x-placement*="left"] $arrow': {
      right: 0,
      marginRight: "-0.9em",
      marginTop: "0.45rem",
      height: "3em",
      width: "1em",
      "&::before": {
        borderWidth: "1em 0 1em 1em",
        borderColor: `transparent transparent transparent ${theme.palette.background.paper}`,
      },
    },
  },
  arrow: {
    position: "absolute",
    fontSize: 7,
    width: "3em",
    height: "3em",
    "&::before": {
      content: '""',
      margin: "auto",
      display: "block",
      width: 0,
      height: 0,
      borderStyle: "solid",
    },
  },
}));

function StatusPopper({orderId, status}) {
  const classes = useStyles();
  const statusQuery = useSetStatus();
  const anchorRef = React.useRef(null);
  const [arrow] = React.useState(true);
  const [open, setOpen] = React.useState(false);
  const [arrowRef, setArrowRef] = React.useState(null);
  const id = open ? "scroll-playground" : null;

  const toggle = () => setOpen((prevOpen) => !prevOpen);

  return (
    <>
      <img
        className={classes.img}
        ref={anchorRef}
        src={sources[status]}
        width="19"
        height="19"
        alt="on-agreement"
        aria-describedby={id}
        variant="contained"
        color="primary"
        onClick={toggle}
      />

      <Popper
        id={id}
        open={open}
        anchorEl={anchorRef.current}
        placement="right"
        disablePortal={true}
        className={classes.popper}
        modifiers={{
          flip: {
            enabled: true,
          },
          preventOverflow: {
            enabled: true,
            boundariesElement: "scrollParent",
          },
          arrow: {
            enabled: true,
            element: arrowRef,
          },
        }}
      >
        {arrow ? <span className={classes.arrow} ref={setArrowRef} /> : null}
        <div className={classes.container}>
          <Button
            className={classes.acceptanceButton}
            onClick={() =>
              statusQuery.mutate({orderId: orderId, statusId: 1, toggle: toggle})
            }
          >
            На погодженні
          </Button>
          <br />
          <Button
            className={classes.inWorkButton}
            onClick={() =>
              statusQuery.mutate({orderId: orderId, statusId: 2, toggle: toggle})
            }
          >
            В роботі
          </Button>
          <br />
          <Button
            className={classes.readyButton}
            onClick={() =>
              statusQuery.mutate({orderId: orderId, statusId: 3, toggle: toggle})
            }
          >
            Готове
          </Button>
          <br />
          <Button
            className={classes.completedButton}
            o
            onClick={() =>
              statusQuery.mutate({orderId: orderId, statusId: 4, toggle: toggle})
            }
          >
            Віддано
          </Button>
        </div>
      </Popper>
    </>
  );
}

export default StatusPopper;
